*,
html {
  scroll-behavior: smooth !important;
  line-height: 1.2;
}
body {
  font-family: TTNorms, sans-serif;
  line-height: 1.2;
  font-weight: 400;
  overflow-x: hidden;
  color: #282828;
}
.main {
  background-size: cover;
  height: 800px;
  color: white;
  background:
        /* top, transparent black, faked with gradient */ linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    /* bottom, image */ url("./images/man_bg.jpg") center no-repeat;
}

header {
}

.container {
  max-width: 370px;
  margin: 0 auto;
}

.phone_block {
  margin: 0 auto;
}

.count-price {
  background-image: linear-gradient(-216deg, #fff0bf 0, #fcdc6e 100%);
  padding: 2.1875vw 2.8125vw;
  box-shadow: 0 43px 101px rgba(245, 219, 117, 0.2),
    inset 0 0 10px rgba(206, 229, 251, 0.15);
  border-radius: 2px;
  cursor: pointer;
  color: #282728;
  border: none;
  position: relative;
  letter-spacing: 1.3px;
  font-weight: 600;
  transition: all 0.4s;
  scroll-behavior: smooth;
  font-size: 1rem;
}

.modalCallInput {
  background-image: none;
}

.quiz_section_content {
  color: black;
}

.circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  box-shadow: 0 43px 101px rgba(245, 219, 117, 0.2),
    inset 0 0 10px rgba(206, 229, 251, 0.15);
  background-color: #393939;
  background-image: linear-gradient(-216deg, #fff0bf 0, #fcdc6e 100%);
  border-radius: 50%;
  margin-right: 1.302vw;
}
.circle > img,
.remove-bg {
  mix-blend-mode: multiply;
  filter: contrast(1);
}

.circle-w-space::before {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 4px;
  margin-top: 8px;
  content: "";
  border: 3px solid #fcdc6e;
  border-radius: 50%;
  /* margin-right: 0.46875vw; */
  /* margin-top: 0.3vw; */
}
.nav {
  padding: 20px;
  width: 100%;
  background-color: #282728;
  flex-direction: column;
  justify-content: flex-start;
  color: #fff;
  overflow-y: auto;
  z-index: 999;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  /* background-color: #fff; */
  transform: translateX(-100%);
  transition: all 0.4s ease-in;
}

.nav.active {
  transform: translateX(0);
}
/* .nav_burger {
  width: 3rem;
  height: 2.1rem;
} */
.nav_burger {
  width: 2rem;
  height: 1.7rem;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  display: flex;
}

.nav_burger_item {
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  position: relative;
  background-color: white;
}

/* .nav__close {
  top: 20px;
  right: 20px;
} */

.nav__close {
  background-color: #fff;
  width: 3rem;
  height: 3rem;

  display: block;
  /* width: 100%; */
  height: 4px;
  border-radius: 4px;
  position: relative;
  background-color: white;
  transform: rotate(45deg);
  margin-top: 20px;
}

.nav__close::before {
  background-color: #fff;
  transform: rotate(90deg);
}

.nav__close::before {
  content: " ";
  position: absolute;
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  position: relative;
  background-color: white;
}

.ul_item {
  position: relative;
  height: 60px;
  display: flex;
}
.ul_item::after {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  bottom: 30%;
  align-items: center;
  /* bottom: 90%; */
  /* padding-bottom: 50px; */
  /* transform: translateX(-50%); */
  /* bottom: -23px; */
}
.last_ul_item:after {
  display: none;
}
