.quiz-form {
  position: relative;
  border-radius: 2px;
  box-shadow: 0 16px 113px 13px rgba(40, 39, 40, 0.1);
}
.quiz-form::before {
  content: "";
  width: 98%;
  height: 98%;
  border-radius: 2px;
  border: 1px solid #e5e5e5;
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.some_btn:hover {
  box-shadow: 0 10px 18px rgba(232, 169, 97, 0.25),
    inset 0 0 0 rgba(255, 255, 255, 0.25);
}

.some_btn {
  width: 10rem;
  height: 3.5rem;
  margin-left: 0;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.par-par {
  position: relative;
  margin-bottom: 1.615vw;
  z-index: 1;
}

.progress-p {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.progress-par {
  position: relative;
  background-color: #f7f7f7;
  width: 100%;
  height: 1.5rem;
  position: relative;
  overflow: hidden;
  /* height: 2.5vw; */
  border-radius: 2px;
}

.progress-child {
  position: absolute;
  left: 0.26vw;
  top: 50%;
  transform: translateY(-50%);
  height: 1.5rem;
  border-radius: 2px;
  background-color: #fcdc6e;
  -webkit-animation: loadingBg 15s infinite linear;
  animation: loadingBg 15s infinite linear;
}

.grey-btn {
  background-color: #f7f7f7;
  background-image: none;
  color: rgba(40, 40, 40, 0.5);
}

.form-check {
  margin-bottom: 1rem;
  width: 100%;
  background-color: #ededed;
  padding: 11px;
  border-radius: 2px;
}

.modal-overlay {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: rgba(0, 0, 0, 0.7);
}

.modal {
  top: 5rem;
  /* max-width: 28rem;
  width: 28rem; */
}
.modal-call {
  position: relative;
  background-color: #282728;
}
.modal-links {
  position: relative;
  background-color: #282728;
}
.modal {
  box-sizing: content-box;
  padding: 20px;
  position: absolute;
  left: 50%;
  transition: all 0.8s ease;
  transform-style: preserve-3d;
  z-index: 77;
  top: 5vw;
  width: 240px;
  transform: translateX(-50%);
  text-align: center;
  margin-bottom: 0;
  border: 0;
  background-size: cover;
  color: #fff;
}

body.modal-active {
  overflow: hidden;
}

.closeCallModal {
  position: absolute;
  top: 5px;
  right: 5px;
}

.footer {
  /* height: 380px; */
  background-color: #f0f0f0;
  padding: 20px;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.call-footer {
  box-sizing: content-box;
  padding: 20px;
  transition: all 0.8s ease;
  transform-style: preserve-3d;
  z-index: 77;
  /* width: 240px; */
  text-align: center;
  margin-bottom: 0;
  border: 0;
  background-size: cover;
  color: #fff;
  background-color: #282728;
}

.modal-overlay-privacy {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #282728;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
  max-width: 500px;
  width: 90%;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.close-button svg {
  fill: #000;
}

.privacy-policy-text {
  margin-top: 20px;
}

.privacy-policy-text h2 {
  margin-bottom: 10px;
}

.real-footer {
  background-color: #282728;
  color: white;
}

.socials-img {
  width: 40px;
  height: 40px;
}

